.container_marquee {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  white-space: nowrap;
  background-color: #1c6dd0;
  color: #fdf6f0;
}
.container_marquee h2 {
  font-size: 18vw;
}
@media screen and (min-width: 900px) {
  .container_marquee h2 {
    font-size: 10vw;
  }
}